@tailwind base;
@tailwind components;
@tailwind utilities;

*,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.scrolled {
  background-color: rgba(50, 50, 50,.9);
}

.black-nav {
    background-color: rgba(50, 50, 50);
}

.about-me {
  background: linear-gradient(90deg, rgba(240,240,240,1) 0%, rgba(140,140,142,1) 100%, rgba(0,212,255,1) 100%);
}

.regular-clip {
  overflow: visible;
  text-overflow: clip;
  white-space: wrap;
}

