@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Alfa+Slab+One&family=Fira+Code:wght@300;400;500;600;700&display=swap');

.banner-section {
  background: black;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Code', monospace;
}

video {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0,.4);
}

.scrolled {
  background-color: rgba(50, 50, 50,.2);
}

.black-nav {
    background-color: rgba(50, 50, 50);

}

.react-multi-carousel-item{
  display: flex;
  align-items: center;
  justify-content: center;
}